@import url(https://fonts.googleapis.com/css2?family=Goldman&display=swap);
body {
  margin: 0;
  font-family: "Goldman", cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.garage-door{background-color:transparent;width:100%;height:100%;position:relative;overflow:hidden}.garage-door-left,.garage-door-right{width:calc(50% + 1px);height:100%;position:absolute;top:0}.garage-door-left{left:-50%}.garage-door-color-picker{position:absolute;bottom:10px;left:10px;text-shadow:0 0 10px rgba(255,255,255,0.75);display:flex;justify-content:center;align-items:center;max-width:45vh}.garage-door-color-picker label{margin-left:10px}.garage-door-right{right:-50%}.garage-door-unlock{width:100px;height:100px;border-radius:100%;box-shadow:0 0 3px 3px white;position:absolute;top:calc(50% - 100px/ 2);right:calc(100% - 100px/ 2);z-index:1;display:flex;justify-content:center;align-items:center;font-size:calc(100px * 0.7);color:rgba(255,255,255,0.5);cursor:pointer}.garage-door-icon{position:absolute;bottom:0;right:0;-webkit-filter:invert(100%) opacity(10%) blur(1px);filter:invert(100%) opacity(10%) blur(1px);height:45vw;width:45vw;max-width:512px;max-height:512px}.garage-door-content{background-color:transparent;position:absolute;width:100%;height:100%}.garage-door-info{position:absolute;width:100%;height:calc(50% - 100px / 2 - 20px);background-color:transparent;display:flex;flex-direction:column;justify-content:center;align-items:center;color:#fff;text-shadow:0 0 10px rgba(0,0,0,0.75);font-size:4vh}

.project-viewer{position:relative;width:100%;height:100%;background-color:white}.project-viewer-title{position:absolute;top:50px;width:100%;text-align:center}.project-viewer-project{position:absolute;top:0;left:0;width:100%;height:100%;display:flex;justify-content:center;align-items:center}.project-viewer-wheel{position:absolute;top:90vh;left:calc((100vw - 80vw) / 2);width:80vw;height:80vw;border-radius:1000px;box-shadow:0 0 3px 3px white}.project-viewer-wheel>div{position:absolute;top:0;text-align:center;width:100%;color:white;font-size:4vh;margin-top:2vh}

.contact-me{position:absolute;left:10px;top:10px;display:flex}.contact-me>button{background-color:transparent;color:inherit;border:none;outline:none;height:60px;width:60px;display:flex;justify-content:center;align-items:center;margin:5px;font-size:45px;border-radius:100px;box-shadow:0 0 3px 3px white;cursor:pointer;transition:box-shadow 0.05s linear}.contact-me>button:active{box-shadow:0 0 5px 5px white}.my-infos{margin-top:95px;text-align:center}

